@import url('https://fonts.googleapis.com/css2?family=Kalam&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navbar {
  font-family: 'Times New Roman', Times, serif;
  font-size: 16px;
  line-height: 25px;
  text-transform: uppercase;
  box-shadow: 0px 12px 8px -8px grey;
  --webkit-box-shadow: 0px 12px 8px -8px grey;
  border-bottom: 0px solid grey !important;
}

.nav-link {
  font-weight: 500px !important;
}

.active {
  border-bottom: 1px solid black;
}

.navbar-brand {
  font-weight: 600;
  font-family: 'Kalam', cursive;
}


/*footer*/
.footer {
  height: 170px;
  color: rgb(255, 255, 255);
  background: rgb(19, 2, 35);
  display: flex;
  flex-direction: row;
  gap: 200px;
}

.footer .name {
  display: flex;
  flex-direction: row;
  margin-top: 50px;
  padding-left: 210px;
  font-size: 25px;
}

.footer a {
  display: flex;
  flex-direction: column;
  text-decoration: none;
  color: rgb(255, 255, 255);
  padding: 10px;
}

.footer a:hover {
  color: rgb(255, 255, 255);
}

.footer .social {
  display: flex;
  flex-direction: row;
  margin-top: 50px;
}

.copy {
  background: rgb(0, 0, 0);
  color: white;
}



/*page not found*/
.page {
  display: flex;
  flex-direction: column;
  min-height: 65vh;
  align-items: center;
  justify-content: center;
}

.pageTitle {
  font-size: 50px;
  font-weight: bolder;
}

.pageButton {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}

/*register page*/
.register {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 70vh;
}


.product-link {
  text-decoration: none !important;
  color: black !important;
}



/*filter column in home*/
.col-md-2 {
  background-color: rgb(250, 248, 248);
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.75);
  clip-path: inset(0px -15px 0px 0px);
}


.form-control {
  width: 500px;
}

.button {
  border: 1px solid black;
  background-color: white;
  width: 70px;
  cursor: pointer;
  border-radius: 7px;
}

.button:hover {
  background-color: black;
  color: white;
}



.col-img {
  width: 200px;
}

.col-img1 {
  width: 200px;
  align-items: center;
}

.card {
  align-items: center;
}

.card-category {
  width: 354px;
  border: 1px solid lightgrey;
  margin: 10px;
}

.card-category-home {
  width: 300px;
  border: 1px solid lightgrey;
  margin: 10px;
}

.product-details {
  display: flex;
  flex-direction: row;
}

.product-card {
  margin-left: 150px;
  margin-top: 70px;
}

.product-details-info {
  margin-top: 55px;
  width: 600px;
}

.heading {
  border: 1px solid black;
}

.margin-home-product {
  margin-left: 135px;
}





.image {
  background-image: url(./images/loginpage.png);
  background-repeat: no-repeat;
  width: 400px;
  background-size: 100%;
  background: linear-gradient(#a8ff78, #78ffd6);
}

.image2 {
  background-image: url(./images/forgetpage.jpg);
  background-repeat: no-repeat;
  width: 400px;
  background-size: 100%;
  background: linear-gradient(#a8ff78, #78ffd6);
}

.loginForm {
  display: flex;
  flex-direction: row;
}

.logincred {
  background: linear-gradient(to bottom right, #12c2e9, #c471ed, #f64f59);
  width: 400px;
}

.registercred {
  background: linear-gradient(to bottom right, #12c2e9, #c471ed, #f64f59);
  width: 700px;
}

.email {
  height: 41px;
  width: 300px;
  margin-left: 50px;
  margin-top: 52px;
  border-radius: 7px;
  border: none;
}

.registeremail {
  height: 41px;
  width: 600px;
  margin-left: 50px;
  margin-top: 52px;
  border-radius: 7px;
  border: none;
}

.pass {
  height: 41px;
  width: 300px;
  margin-left: 50px;
  margin-top: 40px;
  border-radius: 7px;
  border: none;
}

.logincred input {
  text-align: center;
}

.registercred input {
  text-align: center;
}

.forget {
  margin-top: 40px;
  text-align: center;
}

.forget a {
  color: black;
  text-decoration: none;
  font-size: 16px;
}

.forget a:hover {
  color: rgb(10, 12, 109);
}

.loginButton {
  margin-top: 40px;
  margin-left: 50px;
  color: rgb(0, 0, 0);
  border: 1px solid black;
  width: 300px;
  height: 40px;
  border-radius: 7px;
}

.loginButton:hover {
  background-color: black;
  color: white;
}

.registerButton {
  margin-top: 40px;
  margin-left: 210px;
  color: rgb(0, 0, 0);
  border: 1px solid black;
  width: 300px;
  height: 40px;
  border-radius: 7px;
}

.registerButton:hover {
  background-color: black;
  color: white;
}

.registerUser {
  height: 800px;
}